body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16;
  color: rgb(255, 255, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root {
  --navBarBackground: rgb(2, 2, 2);
}
html {
  /*background-image: url('https://wallpapercave.com/dwp1x/2wuJNqK.jpg');*/
  background-image: url('https://images.creativemarket.com/0.1.0/ps/9283063/3690/1214/m1/fpnw/wm1/tskwxrwkfmqvqgto5ev8zo5hhdtpkc2z9tpyuudl5dml7qhilamgnnomjpsi1vzc-.jpg?1603657664&s=a473ede2eca93431ef45717b5d38bb3e');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 
    top 0px left 0px,
    top 0px right 0px,
    bottom 0px right 0px,
    bottom 0px left 0px;
  background-attachment: fixed;
}
#navBar {
  /*background-color: var(--navBarBackground);*/
  display: flex;
  flex-direction: row;
  justify-content: center;
  

}
#navBar button {
  border: none;
  padding: 10px 20px;
  /*background-color: var(--navBarBackground);*/
  background: none;
  cursor: pointer;
}
#navBar button span {
  color: rgb(23, 102, 102);
  font-weight: 900;
  font-size: 20px;
}
#home {
  margin: 60px;
  max-width: 500px;
  margin: auto;
}
#home p {
  text-indent: 18px;
}
#home h1 {
  text-align: center;
}
#photo {
  height: 400px;
  margin: auto;
  border-radius: 50%;
  display: block;
}
.individualProject {
  margin: 20px 40px;
  border-top: white 1px solid;
  padding: 20px;
}
.projectLinks {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.site {
  background-color: black;
   
  text-decoration: none;
  color: white;
  padding: 10px;
  border-radius: 20px;
  transition: .2s;
}
.site:hover {
  transform: scale(1.1);
}

.code {
  background-color: rgb(37, 37, 37);
  color: rgb(0, 114, 28);
  text-decoration: none;
  cursor: pointer;
  padding: 10px;
  border-radius: 20px;
  transition: .2s;
}
.code:hover {
  transform: scale(1.1);
}
#resume {
  width: 600px;
  display: table;
  margin-left: auto;
  margin-right: auto;
}
#contact {
  text-align: center;
  margin: 50px;

}
#contact h3 a {
  text-decoration: none;
  color: inherit;
}
